'use client';

import { useState, useEffect, useCallback, useMemo } from 'react';
import { supabase } from '@/lib/supabase';
import { Item, ItemType } from '@/types';
import { v4 as uuidv4 } from 'uuid';

export function useItemStore() {
    const [items, setItems] = useState<Item[]>([]);
    const [isLoaded, setIsLoaded] = useState(false);

    const fetchItems = useCallback(async () => {
        try {
            const { data, error } = await supabase
                .from('items')
                .select('*')
                .order('created_at', { ascending: false });

            if (error) {
                console.error('Error fetching items:', error);
                setIsLoaded(true);
                return;
            }

            if (data) {
                setItems(data);
            }
        } catch (e) {
            console.error('Supabase connection failed:', e);
        }
        setIsLoaded(true);
    }, []);

    useEffect(() => {
        fetchItems();
    }, [fetchItems]);

    const addItem = async (item: Omit<Item, 'id' | 'created_at'>) => {
        const newItem: Item = {
            ...item,
            id: uuidv4(),
            created_at: new Date().toISOString(),
        };

        // UI optimistic update
        setItems(prev => [newItem, ...prev]);

        const { error } = await supabase.from('items').insert(newItem);
        if (error) {
            console.error('Error adding item:', error);
            // Table might not exist yet if user hasn't run the SQL
        }
        return newItem;
    };

    const deleteItem = async (id: string) => {
        setItems(prev => prev.filter(i => i.id !== id));

        const { error } = await supabase
            .from('items')
            .delete()
            .eq('id', id);

        if (error) {
            console.error('Error deleting item:', error);
        }
    };

    const groupedItems = useMemo(() => {
        return {
            text: items.filter(i => i.type === 'text'),
            color: items.filter(i => i.type === 'color'),
            link: items.filter(i => i.type === 'link'),
            image: items.filter(i => i.type === 'image'),
        };
    }, [items]);

    return {
        items,
        groupedItems,
        isLoaded,
        addItem,
        deleteItem,
        refresh: fetchItems
    };
}
