'use client';

import { useItemStore } from '@/hooks/use-item-store';
import { ThemeToggle } from '@/components/theme-toggle';
import { UnifiedInput } from '@/components/unified-input';
import {
  ContentSection,
  ColorsSection,
  LinksSection,
  ImagesSection
} from '@/components/moodboard-sections';
import { Skeleton } from '@/components/ui/skeleton';

export default function Home() {
  const { groupedItems, isLoaded, addItem, deleteItem } = useItemStore();

  if (!isLoaded) {
    return (
      <div className="min-h-screen bg-background p-8">
        <div className="max-w-6xl mx-auto space-y-12">
          <div className="flex justify-between items-center">
            <Skeleton className="h-8 w-32" />
            <Skeleton className="h-8 w-8 rounded-full" />
          </div>
          <Skeleton className="h-20 w-full rounded-2xl" />
          <div className="space-y-8">
            <Skeleton className="h-40 w-full rounded-xl" />
            <Skeleton className="h-40 w-full rounded-xl" />
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background text-foreground pb-32">
      {/* Top Bar */}
      <header className="fixed top-0 left-0 right-0 z-50 bg-background/80 backdrop-blur-xl border-b border-border/40">
        <div className="max-w-6xl mx-auto px-6 h-16 flex items-center justify-between">
          <h1 className="text-[11px] font-bold uppercase tracking-[0.3em]">Untld Moodboard</h1>
          <ThemeToggle />
        </div>
      </header>

      <main className="pt-32 max-w-6xl mx-auto px-6 space-y-20">
        {/* Input Section */}
        <div className="max-w-2xl mx-auto w-full">
          <UnifiedInput onAdd={addItem} />
        </div>

        {/* Content Flow */}
        <div className="space-y-24">
          <ContentSection items={groupedItems.text} onDelete={deleteItem} />
          <ColorsSection items={groupedItems.color} onDelete={deleteItem} />
          <LinksSection items={groupedItems.link} onDelete={deleteItem} />
          <ImagesSection items={groupedItems.image} onDelete={deleteItem} />
        </div>
      </main>
    </div>
  );
}
