'use client';

import { Item } from '@/types';
import { ExternalLink, Download, Copy, Check, Globe, Trash2 } from 'lucide-react';
import { useState, useEffect } from 'react';
import { cn } from '@/lib/utils';

interface SectionProps {
    items: Item[];
    onDelete: (id: string) => void;
}

export function ContentSection({ items, onDelete }: SectionProps) {
    if (items.length === 0) return <EmptyState label="Content" />;
    return (
        <div className="space-y-4">
            <SectionLabel label="Content" count={items.length} />
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {items.map(item => (
                    <div key={item.id} className="group relative bg-muted/30 border border-border/40 rounded-xl p-4 hover:bg-muted/50 transition-all">
                        <button onClick={() => onDelete(item.id)} className="absolute top-2 right-2 opacity-0 group-hover:opacity-100 p-1.5 text-muted-foreground hover:text-destructive transition-all">
                            <Trash2 className="h-4 w-4" />
                        </button>
                        <p className="text-sm leading-relaxed whitespace-pre-wrap">{item.content}</p>
                    </div>
                ))}
            </div>
        </div>
    );
}

export function ColorsSection({ items, onDelete }: SectionProps) {
    const [copiedId, setCopiedId] = useState<string | null>(null);

    useEffect(() => {
        if (copiedId) {
            const timer = setTimeout(() => setCopiedId(null), 2000);
            return () => clearTimeout(timer);
        }
    }, [copiedId]);

    const handleCopy = (hex: string, id: string) => {
        navigator.clipboard.writeText(hex);
        setCopiedId(id);
    };

    if (items.length === 0) return <EmptyState label="Colors" />;
    return (
        <div className="space-y-4">
            <SectionLabel label="Colors" count={items.length} />
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4">
                {items.map(item => (
                    <div key={item.id} className="group relative bg-card border border-border/40 rounded-xl overflow-hidden hover:shadow-sm transition-all">
                        <button onClick={() => onDelete(item.id)} className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 p-1.5 bg-background/80 backdrop-blur-sm rounded-lg text-muted-foreground hover:text-destructive transition-all">
                            <Trash2 className="h-4 w-4" />
                        </button>
                        <div className="aspect-square w-full" style={{ backgroundColor: item.color_hex || '#000' }} />
                        <div className="p-3">
                            <p className="text-[10px] font-bold uppercase tracking-wider mb-1">{item.color_hex}</p>
                            <p className="text-[10px] text-muted-foreground truncate mb-3">{item.color_name}</p>
                            <button
                                onClick={() => handleCopy(item.color_hex!, item.id)}
                                className="flex items-center justify-center gap-1.5 w-full py-1.5 text-[9px] uppercase font-bold tracking-widest bg-muted/50 hover:bg-muted font-medium rounded-md transition-all"
                            >
                                {copiedId === item.id ? <><Check className="h-3 w-3 text-green-500" /> Copied</> : <><Copy className="h-3 w-3" /> Copy</>}
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export function LinksSection({ items, onDelete }: SectionProps) {
    if (items.length === 0) return <EmptyState label="Links" />;
    return (
        <div className="space-y-4">
            <SectionLabel label="Links" count={items.length} />
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
                {items.map(item => {
                    const domain = new URL(item.content).hostname;
                    return (
                        <div key={item.id} className="group relative flex flex-col bg-card border border-border/40 rounded-xl overflow-hidden hover:shadow-md transition-all">
                            <button onClick={() => onDelete(item.id)} className="absolute top-2 right-2 z-10 opacity-0 group-hover:opacity-100 p-1.5 bg-background/80 backdrop-blur-sm rounded-lg text-muted-foreground hover:text-destructive transition-all">
                                <Trash2 className="h-4 w-4" />
                            </button>
                            <div className="p-4 flex-1">
                                <div className="flex items-center gap-2 mb-2">
                                    {item.favicon ? <img src={item.favicon} alt="" className="h-3 w-3" /> : <Globe className="h-3 w-3 text-muted-foreground" />}
                                    <span className="text-[10px] uppercase font-medium text-muted-foreground tracking-wider">{domain}</span>
                                </div>
                                <h3 className="text-sm font-medium line-clamp-2 mb-4 leading-snug">{item.title || item.content}</h3>
                                <a
                                    href={item.content}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="flex items-center justify-center gap-2 w-full py-2 text-[10px] uppercase font-bold tracking-widest text-muted-foreground hover:text-foreground hover:bg-muted/50 rounded-lg border border-transparent hover:border-border transition-all"
                                >
                                    View Link <ExternalLink className="h-3 w-3" />
                                </a>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}

export function ImagesSection({ items, onDelete }: SectionProps) {
    if (items.length === 0) return <EmptyState label="Images" />;
    return (
        <div className="space-y-4">
            <SectionLabel label="Moodboard" count={items.length} />
            <div className="columns-1 sm:columns-2 lg:columns-3 xl:columns-4 gap-4 space-y-4">
                {items.map(item => (
                    <div key={item.id} className="group relative break-inside-avoid rounded-xl overflow-hidden border border-border/40 hover:shadow-lg transition-all">
                        <div className="absolute top-2 right-2 z-10 flex gap-2 opacity-0 group-hover:opacity-100 transition-opacity">
                            <button
                                onClick={async () => {
                                    try {
                                        const res = await fetch(item.image_url!);
                                        const blob = await res.blob();
                                        const url = window.URL.createObjectURL(blob);
                                        const a = document.createElement('a');
                                        a.href = url;
                                        a.download = `image-${item.id}.jpg`;
                                        a.click();
                                    } catch { window.open(item.image_url, '_blank') }
                                }}
                                className="p-2 bg-background/80 backdrop-blur-sm rounded-lg text-muted-foreground hover:text-primary transition-all"
                            >
                                <Download className="h-4 w-4" />
                            </button>
                            <button onClick={() => onDelete(item.id)} className="p-2 bg-background/80 backdrop-blur-sm rounded-lg text-muted-foreground hover:text-destructive transition-all">
                                <Trash2 className="h-4 w-4" />
                            </button>
                        </div>
                        <img src={item.image_url!} alt="" className="w-full h-auto block transform group-hover:scale-[1.02] transition-transform duration-500" />
                    </div>
                ))}
            </div>
        </div>
    );
}

function SectionLabel({ label, count }: { label: string; count: number }) {
    return (
        <div className="flex items-center gap-3 px-1">
            <h2 className="text-[11px] font-bold uppercase tracking-[0.2em] text-muted-foreground/60">{label}</h2>
            <div className="h-[1px] flex-1 bg-border/40" />
            <span className="text-[10px] font-mono text-muted-foreground/40">{count}</span>
        </div>
    );
}

function EmptyState({ label }: { label: string }) {
    return (
        <div className="space-y-4 opacity-40">
            <SectionLabel label={label} count={0} />
            <div className="py-8 flex flex-col items-center justify-center border border-dashed border-border/60 rounded-2xl">
                <p className="text-[10px] uppercase font-bold tracking-widest">No {label.toLowerCase()} yet</p>
            </div>
        </div>
    );
}
